    handleScroll (e) {
      var direction = e.deltaY > 0 ? 'down' : 'up'
      console.log(direction)
      console.log(this.isDiv1)
      if (direction === 'down') {
        if (this.isDiv1) {
          if (parseInt(this.dataIndex) < parseInt(this.numPages)) {
            this.dataIndex = parseInt(this.dataIndex) + 1
          }
          this.lookUnderscore()
        } else if (this.isDiv2) {
        } else if (this.isDiv3) {
        }
      } else {
        if (this.isDiv1) {
          if (this.dataIndex > 1) {
            this.dataIndex = this.dataIndex - 1
            this.lookUnderscore()
          }
        } else if (this.isDiv2) {
        } else if (this.isDiv3) {
        }
      }
    },
    getNumberText(test) {
      test = test + ''
      // 处理负数相关
      if (test < 0) {
        if (test === '-99999') {
          return '-'
        }
        if (test.length > 3) {
          var num1 = test.substring(1, test.length - 2)
          return '-' + num1 + '.' + test.substring(num1.length + 1, test.length)
        } else if (test.length === 3) {
          return '-0.' + test.substring(1, test.length)
        } else {
          return '0.' + test.substring(1, test.length) + '0'
        }
      } else {
        // 处理正数相关
        if (test.length === 1) {
          return '0.' + test + '0'
        }
        if (test.length === 2) {
          return '0.' + test
        }
        if (test === '0.') {
          return '0.00'
        }
        if (test === '0') {
          return '0.00'
        }
        var num = test.substring(0, test.length - 2)
        if (num.length === 0) {
          num = '0'
        }
        return num + '.' + test.substring(num.length, test.length)
      }
    },

    getZSData() {
      var url = this.baseUrl + '/stock_info/get_zs/'
      this.$http
        .get(url)
        .then(
          response => {
            console.log('连接成功')
            this.zsData = response.body
            // console.log(this.zsData[0].name)
          },
          response => {
            console.log('连接出错Kline')
          }
        )
        .catch(function(response) {
          console.log('连接catch-Kline' + response)
        })
    },
    getHttp() {
      this.getListData()
      this.getWarningData()
      this.getHotData()
      this.getZSData()
    },
    sortByDate(obj1, obj2) {
      const val1 = obj1.deadline
      const val2 = obj2.deadline
      console.log(val1 + 'aaaa' + val2)
      return val1 - val2
    },
