    getListData() {
      var count = parseInt((this.screeHeight * 0.98 - 50) / 21)
      console.log('屏幕宽度' + this.screeHeight)
      console.log('屏幕宽度2' + count)
      // 网络请求数据
      // var url = this.baseUrl + '/stock_info/get_leaderboard/st=1&num=65/'
      // http://127.0.0.1:8080/stock_info/get_leaderboard_sort/page=1&num=20&flush=changepercent&sort=0/
      // var url = this.baseUrl + '/stock_info/get_leaderboard_sort/' + 'page=' + this.dataIndex + '&num=' + count + '&flush=' + this.sorting + '&sort=' + this.tableSort
      var url = 'http://172.18.2.205:8080/stockRanking/findStockRankingFromRedis/' + 'page=' + this.dataIndex + '&num=' + count + '&flush=' + this.sorting + '&sort=' + this.tableSort
      this.$http.get(url).then((response) => {
        // var data = response.body.data
        // for (var i = 0; i < response.body.data.length; i++) {
        //   response.body.data[i].changepercent = parseInt(response.body.data[i].changepercent)
        //   response.body.data[i].trade = parseInt(response.body.data[i].trade)
        //   response.body.data[i].change = parseInt(response.body.data[i].change)
        //   response.body.data[i].volume = parseInt(response.body.data[i].volume)
        //   response.body.data[i].turnoverratio = parseInt(response.body.data[i].turnoverratio)
        //   response.body.data[i].open = parseInt(response.body.data[i].open)
        //   response.body.data[i].high = parseInt(response.body.data[i].high)
        //   response.body.data[i].low = parseInt(response.body.data[i].low)
        //   response.body.data[i].settlement = parseInt(response.body.data[i].settlement)
        //   response.body.data[i].per = parseInt(response.body.data[i].per)
        //   response.body.data[i].amount = parseInt(response.body.data[i].amount)
        // }
        this.numPages = response.body.tatal_page
        this.tableData = response.body
        console.log('连接成功')
      }, (response) => {
        console.log('连接出错')
      }).catch(function(response) {
        console.log('连接catch')
      })
    },
    getText(text) {
      var str = text.toString().split('.')
      if (str[0].length > 8) {
        return str[0].substring(0, str[0].length - 8) + '.' + str[0].substring(str[0].length - 8, str[0].length - 6) + '亿'
      } else if (str[0].length > 4) {
        return str[0].substring(0, str[0].length - 4) + '万'
      } else {
        return text
      }
    },
    toDecimal(x) {
      var f = parseFloat(x)
      if (isNaN(f)) {
        return x
      }
      var ff = Math.round(x * 100) / 100
      var s = ff.toString()
      var rs = s.indexOf('.')
      if (rs < 0) {
        rs = s.length
        s += '.'
      }
      while (s.length <= rs + 2) {
        s += '0'
      }
      return s
    },
    getHotData() {
      var url = this.baseUrl + '/stock_info/get_hots/'
      this.$http.get(url).then((response) => {
        for (var i = 0; i < response.body.data.length; i++) {
          response.body.data[i].tscore = parseInt(response.body.data[i].tscore)
          response.body.data[i].hotscore = parseInt(response.body.data[i].hotscore)
          response.body.data[i].hotprice = parseInt(response.body.data[i].price)
          response.body.data[i].hotChange = parseInt(response.body.data[i].change)
          response.body.data[i].index = parseInt(response.body.data[i].index)
        }
        this.hotsData = response.body.data

        console.log('连接成功')
      }, (response) => {
        console.log('连接出错')
      }).catch(function(response) {
        console.log('连接catch')
      })
    },
    getTest(test) {
      var str = test.toString().split('.')
      if (str[1].length) {
        // console.log(str[0] + str[1])
        return parseInt(str[0] + str[1])
      } else {
        return str
      }
