      this.$router.replace({ name: 'kline', params: { code: this.STOCK.code } })
    },
    getPushWarningKline(row) {
      this.STOCK.code = row.stocknum
      this.$router.replace({ name: 'kline', params: { code: this.STOCK.code } })
    },
    customSort(row) {
      const { prop, order } = row
      this.tableData = this.tableData.filter(data => !data.isheader)
      if (order === 'descending') {
        this.tableData = this.tableData.sort((a, b) => b[prop] - a[prop])
      } else {
        this.tableData = this.tableData.sort((a, b) => a[prop] - b[prop])
      }
      this.tableData.unshift(...this.computedArr)
    },
    getRowClass(scope, index) {
      // console.log('进入了getRowClass' + scope.row.id + '---' + index + '---' + this.activeId)
      return scope.row.id === this.activeId ? 'current-row-active' : ''
    },
    getcellClass(scope) {
      return `${scope.column.property || 'index'}-color`
    },
    getCellStyle(scope) {
      return {
        background: 'black',
        borderColor: '#6B0002',
        borderWidth: '0.8px',
        padding: '0px'
      }
    },
    getHeaderCellStyle(scope) {
      return {
        background: 'black',
        borderColor: '#6B0002',
        padding: '0px',
        height: '20px'

      }
    },
    getHeaderCellStyle2(scope) {
