    },
    setActiveId(row) {
      if (row.id) {
        console.log('点击了705、、、' + row.id)
        this.activeId = row.id
      }
    }
  },
  watch: {
    headerValue: {
      handler(newValue, oldValue) {
        const arr = [
          {
            isheader: true,
            name: '平均',
            chg: 0.5,
            now_price: 100
          },
          {
            isheader: true,
            name: '合计',
            chg: 0.5,
            now_price: 100
