            isheader: true,
            name: '最大值',
            chg: 0.5,
            now_price: 100
          },
          {
            isheader: true,
            name: '最小值',
            chg: 0.5,
            now_price: 100
          },
          {
            isheader: true,
            name: '标准差',
            chg: 0.5,
            now_price: 100
          }
        ]
        this.tableData = this.tableData.filter(data => !data.isheader)
        const computedArr = newValue.map(data => {
          return arr[Number(data) - 1]
        })
        this.computedArr = computedArr
        this.tableData.unshift(...computedArr)
      },
      deep: true
    }
  }
}
